<template>
    <router-link class="shops_list-item-link" :to="`/store/${store.storeId}`">
        <!-- 图片 -->
        <div class="shops_list-image">
            <img :src="store.storeImage">
        </div>
        <!-- 详情 -->
        <div class="shops_list-details">
            <!-- //* header -->
            <div class="module-header">
                <!-- 店名 -->
                <h2 class="shops_list-details-title">{{store.storeName}}-{{store.distance}}-{{store.storeRank}}-{{store.storeSales}}</h2>
                <div class="flex">
                    <!-- 评分 星星 -->
                    <ul class="shops_list-details-stars">
                        <li class="star iconfont icon-star"
                        :class="{'star--active': store.storeRank >= index}"
                        v-for="index in 5" :key="index"></li>
                    </ul>
                    <!-- 人均消费 -->
                    <div class="shops_list-details-cost">&yen;{{store.costPerCapital}}/人</div>
                </div>
            <!-- 商店 type -->
            <div class="module-row-space_between">
                <ul class="shops_list-details-types">
                    <li class="item"
                        v-for="(type, index) in store.storeTypes" :key="index">{{type}}</li>
                </ul>
                <!-- 当前人气 -->
                <span class="shops_list-details-popularity">当前人气{{store.storePopularity}}</span>
            </div>
            </div>
            <!-- //* header end -->
            <!-- 商店标签 -->
            <ul class="shops_list-details-labels module-row">
                <li class="item"
                    v-for="(item, index) in store.storeLabels" :key="index"
                    :class="{'label--active': item.isActive}">{{item.label}}</li>
            </ul>
            <!-- 团购信息 -->
            <div class="module-row" v-if="store.groupPurchaseInfo">
                <span class="item group-logo">团</span>
                <span>{{store.groupPurchaseInfo}}</span>
            </div>
            <!-- 券信息 -->
            <div class="module-row" v-if="store.discountInfo">
                <span class="item discount-logo">券</span>
                <span>{{store.discountInfo}}</span>
            </div>
        </div>
    </router-link>
</template>

<script>
export default {
    name: 'ShopslistItem',
    data() { 
        return {

        }
    },
    props: {
        store: {
            type: Object,
            default: {}
        }
    },
}
</script>

<style lang="less">
@import "~@/assets/css/mixin.less";

.shops_list-item{
    @headerHeight: 60px;
    border-bottom: 1px solid @border-color-divition;
    background-color: @bg-white;
    &-link{
        .padding-horizontal(@padding-lg);
        .padding-vertical(@padding-lg);
        .flex();

    }
    .font-size-rem(@font-size-sm);
    // * image
    .shops_list-image{
        // .flex-basis(80px);
        .flex-shrink(0);
        // .image-bg(80px);
        .image-wrapper(@headerHeight);
        height: @headerHeight;
    }
    // * details
    .shops_list-details{
        .flex-grow(1);
        .margin-left-rem(@margin-base);
        .module-header{
            height: @headerHeight;
            .flex-column();
            .justify-content(space-between);
        }
        .module-row-space_between{
            .flex();
            .justify-content(space-between);
        }
        .module-row{
            .flex();
            .align-items(center);
            .item{
                .flex-shrink(0);
            }
            .margin-top-rem(@margin-base);
        }
        // 店名
        &-title{
            .font-size-rem(@font-size-root,bold);
            color: @text-color-title;
        }
        // 评分 星星
        &-stars{
            .flex();
            .star {
                margin-right: 2px;
                font-size: 12px;
                &.star--active {
                    color: @color-primary;
                }
            }
        }
        // 人均消费
        &-cost{
            margin-left: 4px;
        }
        // 当前人气
        &-popularity{
            color: @color-primary;
        }
        // 商店标签
        &-labels,
        &-types{
            .flex();
            & .item{
                margin-right: 10px;
            }
            .label--active{
                color: @color-primary;
                border: 1px solid @color-primary;
                padding: 0 3px;
            }
        }
        &-types{
            .item{
                .flex-shrink(0);
            }
        }
        // 团购信息 劵信息
        .group-logo,
        .discount-logo{
            margin-right: 4px;
            // padding: 2px;
            color: #fff;
        }
        .group-logo{
            border: 1px solid @color-primary;
            background-color: @color-primary;

        }
        .discount-logo{
            border: 1px solid #ff7198;
            background-color:  #ff7198;

        }
    }
}
</style>