import axios from 'axios'

const state = {
    goods: [],
    seller: [],
    ratings: [],
    foodsInCart: [],
}

const getters = {
}

const mutations = {
    setSeller(state, seller){
        state.seller = seller
    },
    setGoods(state, goods){
        state.goods = goods
    },
    setRatings(state, ratings){
        state.ratings = ratings
    },
    setFoodsInCart(state, foods){
        state.foodsInCart = foods
    }
}

const actions = {
    requestStoreData({ commit }, storeId){
        axios.post('/api/store', { storeId })
            .then(res => {
                commit('setSeller', res.data.seller)
                commit('setGoods', res.data.goods)
                commit('setRatings', res.data.ratings)
            })
            .catch(err => console.error(err))
    }
}

export default {
    namespaced: true,
    state,
    getters,
    mutations,
    actions,
}