import axios from 'axios'
import { compareObject } from '../../utils/util'

const state = {
    shops: [],
    // 筛选项
    type: '全部美食',
    location: '全城',
    service: '全部筛选',
    sortType: '智能排序'
}

const getters = {
    sortShops(state){
        const newArr = []
        newArr.push(...state.shops)
        switch(state.sortType){
            case '智能排序': {
                return state.shops
            }
            case '离我最近': {
                return newArr.sort(compareObject('distance'))
            }
            case '好评优先': {
                return newArr.sort(compareObject('storeRank', -1))
            }
            case '销量最高': {
                return newArr.sort(compareObject('storeSales', -1))
            }
            default: return state.shops
        }
    },
    filterShops(state, getters){
        return getters.sortShops.filter(shopsItem => {
            return (shopsItem.storeTypes.indexOf(state.type) !== -1 || state.type === '全部美食')
                && (shopsItem.storeLocation === state.location || state.location === '全城')
                && (shopsItem.storeServices.indexOf(state.service) !== -1 || state.service === '全部筛选')
        })
    }
}

const mutations = {
    setShops(state, shops){
        state.shops = shops
    },
    setType(state, type){
        state.type = type
    },
    setLocation(state, location){
        state.location = location
    },
    setService(state, service){
        state.service = service
    },
    setSorttype(state, sortType){
        state.sortType = sortType
    },
}

const actions = {
    requestShops({ commit }){
        axios.post('/api/shops-list')
            .then(res => {
                commit('setShops', res.data)
            })
            .catch(err => console.log(err))
    }
}

export default {
    namespaced: true,
    state,
    getters,
    mutations,
    actions
}