/**
 * 根据对象的属性(值为数字或可以转换为数字)排序
 * @param {String} keyName 要排序的属性名
 * @param {Number} sortOrder 1 为升序 -1 为降序, 默认为升序
 * @example arr.sort(compareObject(keyName, -1))
 */
export const compareObject = (keyName, sortOrder = 1) => {
    return (prevObj, nextObj) => {
        let prevValue = parseFloat(prevObj[keyName])
        let nextValue = parseFloat(nextObj[keyName])
        if(prevValue < nextValue){
            return sortOrder === 1 ? -1 : 1
        }else if(prevValue > nextValue){
            return sortOrder === 1 ? 1 : -1
        }else{
            return 0
        }
    }
}