import axios from 'axios'

const state = {
    obj: {},
    type: 'tuijian',
}
const getters = {
    findsFilter (state) {
        return state.obj[state.type]
    }
}
const mutations = {
    setObj (state, obj) {
        state.obj = obj
    },
    addObj (state, value) {
        state.obj[state.type] = value
    },
    setType (state, type) {
        state.type = type
    }
}
const actions = {
    requestFinds ({ commit, state }) {
        // obj.type 数组为空时, 才请求数据
        // if(!Object.keys(state.obj[state.type]).length > 0){
        if(!state.obj[state.type].length > 0){
            axios.post('/api/findlist', {
                    type: state.type
                })
                .then(res => {
                    commit('addObj', res.data)
                })
                .catch(err => {
                    throw err
                })
        }
    },
    loadMore({ commit, state }) {
        const newArr = state.obj[state.type].concat()
        axios.post('/api/findlist', {
                type: state.type
            })
            .then(res => {
                newArr.push(...res.data)
                commit('addObj', newArr)
            })
            .catch(err => {
                throw err
            })
    }
}

export default {
    namespaced: true,
    state,
    getters,
    mutations,
    actions
}