import axios from 'axios'
import sesstionStorage from '../../utils/sesstionStorage'

const state = {
    orders: [],
    orderState: '全部'
}

const getters = {
    ordersFilter (state) {
        if(state.orderState === '全部') return state.orders
        return state.orders.filter(order => order.orderState === state.orderState)
    }
}

const mutations = {
    setOrders (state, orders) {
        state.orders = orders
    },
    setOrderState (state, orderState) {
        state.orderState = orderState
    },
    getOrderState (state) {
        let value = sesstionStorage.getSession('orderState')
        if(value){
            state.orderState = value
        }
    }
}

const actions = {
    getOrders ({ commit, state }) {
        axios.post('/api/order-list', {
                orderState: state.orderState
            })
            .then(res => {
                commit('setOrders', res.data)
            })
            .catch(err => console.log(err))
    }
}

export default {
    namespaced: true,
    state,
    getters,
    mutations,
    actions
}