import Vue from 'vue'
import Router from 'vue-router'
import Home from './components/home/Home.vue'
import store from './store'
import VueCookie from 'vue-cookie'

Vue.use(Router)

const router = new Router({
  mode: 'history',
  base: '/meituan-app/',
  routes: [
    {
      path: '/', // * 首页
      redirect: '/home',
    },
    {
      path: '/home', // * 首页
      alias: '/',
      name: 'home',
      component: Home
    },
    {
      path: '/shops-list', // 商店列表
      name: 'shops-list',
      component: () => import('./components/home/Shops.vue')
    },
    {
      path: '/store/:id', // 商店详情
      name: 'store',
      component: () => import('./components/home/Store.vue')
    },
    {
      path: '/goods-details/:id',
      name: 'goods-details',
      component: () => import('./components/home/StoreGoodsDetails.vue')
    },
    {
      path: '/find', // * 发现
      name: 'find',
      component: () => import('./components/find/Find.vue'),
      children: [
        {
          path: '/find/:type',
          component: () => import('./components/find/FindList.vue')
        }
      ]
    },
    {
      path: '/order', // * 订单
      name: 'order',
      component: () => import('./components/order/Order.vue'),
      meta: {loginAuth: true},
      children: [
        {
          path: '/order/:orderState',
          component: () => import('./components/order/OrderList.vue')
        }
      ]
    },
    {
      path: '/mine', // * 我的
      name: 'mine',
      component: () => import('./components/mine/Mine.vue'),
      meta: {loginAuth: true}
    },
    // * 登陆
    {
      path: '/login',
      name: 'login',
      component: () => import('./components/base/Login.vue')
    },
    {
      path: '/login/inputAuthcode',
      name: 'inputAuthcode',
      component: () => import('./components/base/LoginInputAuthcode.vue')
    },
    // * 未登录
    {
      path: '/notlogin',
      name: 'notlogin',
      component: () => import('./components/base/NotLogin.vue')
    }
  ],
})
router.beforeEach((to, from, next) => {
    const toPath = to.fullPath
    // 获取 cookie
    if(VueCookie.get('userPhone')) store.dispatch('login/getLocalUphone')
    switch(toPath){
      case '/order': {
        if(store.state.login.userPhone){ // 已登录
          next(`/order/${store.state.order.orderState}`)
        }else{
          next()
        }
        break
      }
      default: next()
    }
  }
)

export default router