<template>
    <section class="shops-list">
        <ul class="shops_list">
            <li class="shops_list-item"
                v-for="store in filterShops" :key="store.storeId"
            >
                <ShopslistItem :store="store"/>
            </li>
        </ul>
    </section>
</template>

<script>
import ShopslistItem from './ShopslistItem.vue'
import { mapGetters, mapActions } from 'vuex'
export default {
    name: 'ShopList',
    components: {
        ShopslistItem
    },
    data() { 
        return {
        }
    },
    computed: {
        ...mapGetters('shops', [
            'filterShops'
        ]),
    },
    methods: {
        ...mapActions('shops', [
            'requestShops'
        ])
    },
    created(){
        // 初始化数据
        this.requestShops()
    }
}
</script>

