import VueCookie from 'vue-cookie'
import axios from 'axios'

const state = {
    user: {},
    userPhone: '',
    password: '',
}

const getters = {

}

const mutations = {
    setUser (state, { user }) {
        state.user = user
    },
    setUserPhone (state, phone) {
        state.userPhone = phone
    },
    setPassword (state, password) {
        state.password = password
    },
}

const actions = {
    getLocalUphone ({ commit }) {
        let phone = VueCookie.get('userPhone')
        commit('setUserPhone', phone)
    },
    getUser ({ commit, state }) {
        axios.post('/api/users', { userPhone: state.userPhone }).then(res => {
                commit('setUser', { user: res.data.user })
            })
            .catch(err => console.log(err))
    }
}

export default {
    namespaced: true,
    state,
    getters,
    mutations,
    actions
}