<template>
    <Layout>
        <template v-slot:header>
            <div class="search_box">
                <el-input class="search-input" v-model="input" placeholder="请输入内容"></el-input>
            </div>
        </template>
        <div class="home-content">
            <!-- // * 首页 轮播图 -->
            <section class="banner">
                <swiper class="swiper-wrapper"
                    :options="swiperOption"
                    ref="mySwiper"
                    v-if="banners.length!=0"
                >
                    <swiper-slide v-for="(item,index) in banners" :key="index" >
                        <router-link :to="item.link">
                            <img :src="item.src" :alt="item.alt" />
                        </router-link>
                    </swiper-slide>
                    <div class="swiper-pagination" slot="pagination"></div>
                </swiper>
            </section>
            <!-- 首页 轮播图 end -->
            <!-- // * 分类 -->
            <section class="types-wrapper">
                <ul class="types">
                    <router-link class="types-item"
                        tag="li"
                        :to="item.route"
                        v-for="(item) in types" :key="item.label"
                    >
                        <a>
                            <div class="image" :style="{'background-color': item.bgcolor}">
                                <i :class="['iconfont',item.icon]"></i>
                            </div>
                            <div class="label">{{item.label}}</div>
                        </a>
                    </router-link>
                    <!-- 3 个空元素 -->
                    <li v-for="n in 3" :key="n" class="empty"></li>
                </ul>
            </section>
            <!-- 分类 end -->
            <!-- // * 附近商家 -->
            <Shopslist/>
            <!-- 附近商家 end -->
        </div>
    </Layout>
</template>

<script>
import Layout from '@/components/layout/Layout.vue'
import Shopslist from '@/components/home/Shopslist.vue'

// swiper 轮播插件
import { swiper, swiperSlide } from 'vue-awesome-swiper'
import 'swiper/dist/css/swiper.css'

export default {
    name: 'Home',
    components: {
        Layout,
        Shopslist,
        swiper,
        swiperSlide
    },
    data() { 
        return {
            input: '',
            swiperOption: {
                autoplay: true,
                delay: 1000,
                loop: true,
                pagination: {
                    el: '.swiper-pagination'
                }
            },
            banners: [
                {
                    src: require('./img/banner-mid-1.png'),
                    link: '#',
                    alt: '图片1',
                },
                {
                    src: require('./img/banner-mid-2.jpeg'),
                    link: '#',
                    alt: '图片2',
                },
                {
                    src: require('./img/banner-mid-3.jpeg'),
                    link: '#',
                    alt: '图片3',
                },
                {
                    src: require('./img/banner-mid-4.png'),
                    link: '#',
                    alt: '图片4',
                },
            ],
            types: [
                {
                    icon: 'icon-meishi',
                    label: '美食',
                    route: '/shops-list',
                    bgcolor: `#fe6e73`
                },
                {
                    icon: 'icon-chaoshi',
                    label: '美团超市',
                    route: '/shops-list',
                    bgcolor: '#7cacfb'
                },
                {
                    icon: 'icon-fruits',
                    label: '生鲜果蔬',
                    route: '/shops-list',
                    bgcolor: '#91df62'
                },
                {
                    icon: 'icon-hedrink46',
                    label: '甜点饮品',
                    route: '/shops-list',
                    bgcolor: '#f774d4'
                },
                {
                    icon: 'icon-youxuan',
                    label: '正餐优选',
                    route: '/shops-list',
                    bgcolor: '#ffaf56'
                },
                {
                    icon: 'icon-bike',
                    label: '美团专送',
                    route: '/shops-list',
                    bgcolor: '#fecf64'
                },
                {
                    icon: 'icon-canpinhui-xican',
                    label: '能量西餐',
                    route: '/shops-list',
                    bgcolor: '#ffaf56'
                },
                {
                    icon: 'icon-xiaochi',
                    label: '精品小吃',
                    route: '/shops-list',
                    bgcolor: '#5cd4e0'
                },
            ]
        }
    }
}
</script>

<style lang="less">
@import "~@/assets/css/mixin.less";
// header
.search_box {
    .padding-horizontal(20px);
    .padding-vertical(15px);
    background-color: @bg-white;
}
.home-content{
    .banner,
    .types-wrapper {
        .module();
    }
    // * banner
    .banner {
        overflow: hidden;
    }
    // * 分类
    .types { // ul
        .flex-sb-center(wrap);
        .types-item { // li
            width: 25%;
            .padding-bottom-rem(@padding-base);
            &>a {
                display: block;
                width: 100%;
                .padding-top-rem(@padding-base);
                .flex-column-center-sa();
                .image {
                    .width-rem(50px);
                    .height-rem(50px);
                    .flex-center();
                    color: #fff;
                    border-radius: 50%;
                    i {
                        .font-size-rem(25px);
                    }
                }
                .label {
                    margin-top: 8px;
                    font-size: @font-size-base;
                    color: @text-color-menu_nav;
                }
            }
        }
        .empty {
            width: 25%;
        }
    }
}
</style>