<template>
    <div class="container">
        <header class="header" v-if="!closeHeader">
            <slot name="header"></slot>
        </header>
        <main class="main"
            @scroll="handleScroll"
        >
            <slot></slot>
        </main>
        <!-- 默认为 Tabbar 组件 -->
        <footer class="footer" v-if="!closeTabbar">
            <slot name="footer">
                <Tabbar />
            </slot>
        </footer>
    </div>
</template>

<script>
import Tabbar from './Tabbar.vue'
import { mapState, mapMutations } from 'vuex'

export default {
    name: 'TheLayout',
    components: {
        Tabbar
    },
    props: {
        closeHeader: {
            type: Boolean,
            default: false
        },
        closeTabbar: {
            type: Boolean,
            default: false
        }
    },
    data() { 
        return {

        }
    },
    computed: {
        ...mapState('scroll', {
            scrollTop: state => state.scrollTop
        })
    },
    methods: {
        ...mapMutations('scroll', [
            'setScrollTop'
        ]),
        handleScroll(event){
            this.setScrollTop(event.target.scrollTop)
        }
    },
}
</script>

<style lang="less">
@import "~@/assets/css/mixin.less";
html,
body,
#app {
    height: 100%;
    overflow: hidden;
}
.container{
    height: 100%;
    .flex-column();
    header.header {
        flex-shrink: 0;
    }
    .main {
        background-color: @bg-base;
        flex-grow: 1;
        overflow-y: scroll;
        overflow-x: hidden;
    }
    footer.footer {
        flex-shrink: 0;
    }
}
</style>